.users__container{
    grid-area: main;
}
.users__content h1{
    font-size: 40px;
    margin-bottom: 30px;
}
.user__container{

    margin-bottom: 10px;
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    width: 500px;
}
.user__follow-btn button{
    color: #fff;
    border-radius: 5px;
    padding: 5px 10px;
    width: 100%;
    background-color: red;
    transition: .3s;
}

.user__follow-btn button:hover{
    color: red;
    background-color: #000;
}
.user__follow-btn button:active{
    color: #fff;
}
.right-block{
    padding-left: 40px;
    width: 100%;
    position: relative;
} 
.info__left__block{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.user__info{
    width: 100%;
    justify-content: space-between;
    display: flex;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.user__status{
    font-size: 15px;
}
.user__location{
    text-align: end;
}
.user__ava{
    width: 80px;
    margin-bottom: 10px;
}
.user__ava img{
    border-radius: 50%;
    max-width: 100%;
}
.user__follow-btn button.followed__button{
    background-color: green;
}
.user__follow-btn button.followed__button:hover{
    color: #000;
    background-color: #fff;
}
.user__follow-btn button.followed__button:active{
    background-color:red;
}
.user__follow-btn button:disabled,
button[disabled]{
    pointer-events: none;

}
.pagination__container{
    margin-top: 20px;
    margin-bottom: 20px;
}
