.pages__container{
    padding: 0px 20px;
    margin-bottom: 15px;
    flex-wrap: wrap;
    display: flex;
}
.pages__container div{
    cursor: pointer;
    margin-right: 5px;
}
.pagination__container div.active{
    font-weight: bold;
} 
.navigation{
	display: flex;
}
.navigation li a{
    margin: 10px;
    cursor: pointer;

}
